<template>
  <v-card
    flat
  >
    <!-- tabs -->
    <v-tabs
      v-model="tab"
      show-arrows
    >
      <v-tab
        v-for="tab in tabs"
        :key="tab.icon"
      >
        <v-icon
          size="30"
          class="me-3"
        >
          {{ tab.icon }}
        </v-icon>
        <span>{{ tab.title }}</span>
      </v-tab>
    </v-tabs>

    <!-- tabs item -->
    <v-tabs-items v-model="tab">
      <v-tab-item>
        <v-alert
          v-show="showAlert"
          dense
          text
          type="success"
        >
          Successfully marked purchase order as moving from <strong>Pier to Warehouse</strong>. You will be automatically
          redirected in a bit.
        </v-alert>
        <v-form class="multi-col-validation">
          <v-card-text class="pt-5">
            <v-row>
              <v-col cols="6">
                <v-img
                  class="mt-10"
                  aspect-ratio="1.7"
                  max-height="250"
                  contain
                  lazy-src="https://picsum.photos/id/11/10/6"
                  src="https://picsum.photos/id/11/500/300"
                ></v-img>
              </v-col>
              <v-col cols="6">
                <v-card-text class="mt-10">
                  <v-simple-table
                    fixed-header
                  >
                    <v-subheader>
                      Subheader
                    </v-subheader>
                    <template v-slot:default>
                      <thead>
                      <tr>
                        <th class="text-left">
                          Material Name
                        </th>
                        <th class="text-left">
                          Requested Quantity
                        </th>
                      </tr>
                      </thead>
                      <tbody>
                      <tr
                        v-for="(item, key) in orderItems"
                        :key="item.name"
                      >
                        <td>{{ item.name }}</td>
                        <td>{{ item.quantity }}</td>
                      </tr>
                      </tbody>
                    </template>
                  </v-simple-table>
                </v-card-text>
              </v-col>
              <v-col cols="12" class="mt-10">
                <card-supplier-details
                  title="Supplier Details"
                  :details="supplier"
                />
              </v-col>
              <v-col cols="12" class="mt-10 mb-6">
                <h2 class="font-weight-black mb-2 text-uppercase">Shipping Details</h2>
                <v-divider />
              </v-col>
              <v-col
                cols="6"
              >
                <v-text-field
                  v-model="optionsLocal.container_number"
                  outlined
                  dense
                  label="Container Number"
                  :readonly="mode === 'show'"
                  hide-details="auto"
                  :persistent-placeholder="mode === 'show'"
                ></v-text-field>
              </v-col>
              <v-col cols="6">
                <v-text-field
                  v-model="optionsLocal.eta"
                  outlined
                  dense
                  label="Estimated Date and Time of Arrival"
                  :readonly="mode === 'show'"
                  hide-details="auto"
                  :persistent-placeholder="mode === 'show'"
                  type="datetime-local"
                ></v-text-field>
              </v-col>
              <v-col
                cols="6"
              >
                <v-text-field
                  v-model="optionsLocal.courier_name"
                  outlined
                  dense
                  label="Shipping Company"
                  :readonly="mode === 'show'"
                  hide-details="auto"
                  :persistent-placeholder="mode === 'show'"
                ></v-text-field>
              </v-col>
              <v-col
                cols="6"
              >
                <v-text-field
                  v-model="optionsLocal.courier_number"
                  outlined
                  dense
                  label="Shipping Info"
                  :readonly="mode === 'show'"
                  hide-details="auto"
                  :persistent-placeholder="mode === 'show'"
                ></v-text-field>
              </v-col>
            </v-row>
          </v-card-text>

          <v-card-text class="mt-10">
            <v-btn
              outlined
              class="mr-3"
              color="primary"
              @click="save"
            >
              Mark as Pier to Warehouse
            </v-btn>
            <v-btn
              outlined
              type="reset"
              @click.prevent="goBack"
            >
              Back to List
            </v-btn>
          </v-card-text>
        </v-form>
      </v-tab-item>
      <v-tab-item>
        <purchase-order-logs :order-id="id"></purchase-order-logs>
      </v-tab-item>
      <v-tab-item>
        <purchase-order-payment-logs :order-id="id"></purchase-order-payment-logs>
      </v-tab-item>
    </v-tabs-items>
  </v-card>
</template>

<script>
import {
  computed, nextTick, ref, watch, onMounted,
} from '@vue/composition-api'
import router from '@/router'
import store from '@/store'
import {
  IN_TRANSIT_PURCHASE_ORDER,
  GET_SUPPLIER, PIER_TO_WAREHOUSE_PURCHASE_ORDER,
} from '@/store/actions.type'
import PurchaseOrdersService from '@/utils/services/purchase-orders.service'
import { mdiBookInformationVariant, mdiClipboardListOutline, mdiCurrencyUsd, mdiBarcode } from '@mdi/js'
import PurchaseOrderLogs from '@/views/pages/purchase-orders/purchase-order/logs/PurchaseOrderLogs'
import PurchaseOrderPaymentLogs
  from '@/views/pages/purchase-orders/purchase-order/logs/PurchaseOrderPaymentLogs';
import CardSupplierDetails from '@/views/pages/purchase-orders/component/CardSupplierDetails.vue'

export default {
  components: {
    PurchaseOrderPaymentLogs,
    PurchaseOrderLogs,
    CardSupplierDetails
  },
  setup() {
    const { id } = router.history.current.params
    const errors = computed(() => store.getters.errors)
    const optionsLocal = ref({})
    const supplier = ref({})
    const showAlert = ref(false)
    const orderItems = ref([])
    const tab = ref('')

    onMounted(async () => {
      await PurchaseOrdersService.find(id)
        .then(response => {
          const { data } = response.data
          console.log(data)
          optionsLocal.value = {
            description: data.order.description,
            fx_number: data.order.fx_id,
            eta: data.order.eta,
            total: data.order.total,
            paid_amount: data.order.paid_amount,
            items: {},
          }

          supplier.value = data.supplier
          orderItems.value = data.items
        })
    })

    const goBack = () => {
      router.push('/purchase-orders')
    }

    const resolveFormSuccess = () => {
      showAlert.value = true
      setTimeout(() => {
        goBack()
      }, 5000)
    }

    const save = () => {
      const currentForm = optionsLocal.value
      currentForm.id = router.history.current.params.id

      store.dispatch(PIER_TO_WAREHOUSE_PURCHASE_ORDER, currentForm).then(data => {
        if (data != null) {
          resolveFormSuccess()
        }
      })
    }

    // tabs
    const tabs = [
      {title: 'Purchase Order Information', icon: mdiBookInformationVariant},
      {title: 'Purchase Order Logs', icon: mdiClipboardListOutline},
      {title: 'Purchase Order Payment Logs', icon: mdiCurrencyUsd},
    ]

    return {
      id,
      tabs,
      tab,
      optionsLocal,
      orderItems,
      goBack,
      save,
      showAlert,
      errors,
      supplier,
    }
  },
}
</script>
